*{padding:0;margin:0}.ui-view{width:300px;-webkit-transition:all .3s ease;transition:all .3s ease}.fade-enter{opacity:1;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)}.fade-leave{opacity:0;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}.nav{margin:10px 0;overflow:hidden;position:fixed;width:300px;top:50px}.nav li{line-height:30px;background:#42b983;list-style:none;width:25%;float:left}.nav li a{display:block;color:#fff !important;border-right:1px solid #fff}.nav li:last-of-type a{border-right:none}a.nav-active{background:red}



